<template>
  <div class="foucs">
    <ul class="foucs-list">
      <li></li>
      <li></li>
      <li></li>
      <li></li>
    </ul>
    <div class="mplay" ref="mplay" @click="mplaySwitch">
      
    </div>
  </div>
</template>

<script>
export default {
  name:'Focus',
  data () {
    return {
      mplayStyle:{},//样式
      Angle:0,
      timeId:{},
    }
  },
  props: {
    isPlay:{
      type:Boolean,
      default(){
        return false
      }
    },
  },
  methods: {
    mplay(){
      this.timeId = setInterval(() => {
        this.Angle++
        if(this.Angle >= 360){
          this.Angle = 0;
        }
        this.setTransform(this.Angle)
      }, 30);
    },
    handleDom(){
      let mplayEl = this.$refs.mplay;
      this.mplayStyle = mplayEl.style;
    },
    setTransform(position) {
      this.mplayStyle.transform = `rotate(${position}deg)`;
      this.mplayStyle['-webkit-transform'] = `rotate(${position}deg)`;
      this.mplayStyle['-ms-transform'] = `rotate(${position}deg)`;
    },
    mplaySwitch(){
      if(this.isPlay){
        clearInterval(this.timeId);
      }else(
        this.mplay()
      )
      this.$emit('audioClick')
    }
  },
  mounted () {
    this.mplay()
    this.handleDom()
  },
}
</script>

<style scoped>
ul, li{
  list-style: none;
}
.foucs{
  position: absolute;
  width: 50px;
  height: 210px;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.foucs-list{
  width: 50px;
  height: 160px;
  text-align: center;
}
.foucs-list li{
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
  margin: 25px 20px;
}
.mplay{
  height: 50px;
  width: 50px;
  background: url(~assets/imgs/mplay.png) no-repeat 0 0;
  position: absolute;
  bottom: 0;
  border-radius: 50%;
  animation: mplay 5s infinite;
}

</style>