<template>
  <div id="app">
    <foucs @audioClick="audioClick" :isPlay="isPlay"></foucs>
    <frist-page></frist-page>
    <two-page></two-page>
    <three-page></three-page>
    <four-page></four-page>
    <audio ref="audio" autoplay>
      <source src="~assets/music/追忆冰袋辣条 - Story Of A Lonely Man.mp3" type="audio/mpeg">
    </audio>
  </div>
</template>

<script>
import FristPage from 'components/FristPage'
import TwoPage from 'components/TwoPage'
import ThreePage from 'components/ThreePage'
import FourPage from 'components/FourPage'
import Foucs from './components/Focus'

export default {
  name: 'App',
  data () {
    return {
      isPlay:true,
      audioStyle:{},
      audioEl:{},
    }
  },
  components: {
    FristPage,
    TwoPage,
    ThreePage,
    FourPage,
    Foucs,
  },
  methods: {
    audioClick(){
      this.isPlay = !this.isPlay;
      this.isPlay ? this.audioEl.play() : this.audioEl.pause();
    },
    handleDom(){
      this.audioEl = this.$refs.audio;
      this.audioStyle = this.audioEl.style;
    },
  },
  mounted () {
    this.handleDom();
  },
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
}
#app {
  width: 100%;
  height: 100%;
}
</style>
